@import url(https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap);


body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{font-family:"Inter",sans-serif !important}

.transition-short{transition:.5s !important}.transition-medium{transition:.5s !important}.transition-long{transition:.5s !important}.landing-div{width:100vw;height:100vh;background:linear-gradient(180deg, #212C4A 0%, #02091B 100%)}.grid-wrapper{padding-top:.7rem;height:100%;display:grid;grid-template-columns:10rem auto 10rem;grid-gap:0px;grid-template-rows:70px auto 70px}.grid-wrapper .title{position:relative;color:#fff;grid-column:2;grid-row:2;display:flex;flex-direction:column;justify-content:center;align-items:start;padding:7rem;line-height:0px}.grid-wrapper .title h1{font-size:4rem;margin-bottom:2rem;font-weight:800}.grid-wrapper .title h3{line-height:-moz-block-height;font-weight:500;font-size:2rem}.grid-wrapper .title #learnMoreButton{padding:10px 30px 10px 30px;border-radius:.2rem;color:#202c4a;font-weight:600;cursor:pointer;border:none;background:#3accd1}.grid-wrapper .title #learnMoreButton:hover{background:#fff}.grid-wrapper .title .diag-decorator{background:#202c4a;height:110vh;width:450px;z-index:1;position:absolute;right:0px;-webkit-transform:skewX(-20deg);transform:skewX(-20deg)}.grid-wrapper .title img#robotMain{position:absolute;z-index:2;height:95%;min-height:200px;right:0px}.grid-wrapper .nav{grid-column:2;grid-row:1;color:#fff;z-index:2}.grid-wrapper .logo{grid-column:1;grid-row:1;position:relative;display:flex;justify-content:center;align-items:center;top:1rem}.grid-wrapper .language{z-index:2;grid-column:3;grid-row:1;display:flex}.grid-wrapper .social{position:relative;bottom:100px;grid-column:1;grid-row:3;display:flex;flex-direction:column-reverse;color:#fff;align-items:center}.grid-wrapper .social img{width:20px;margin:1.5rem 0px 0rem 0px}.grid-wrapper .landing{grid-column:2;grid-row:2}#decoration{display:block;background:#fff;width:1px;height:20px;padding:150px 0px 50px 0px}
*{list-style:none}.navbar-div{padding:0px 4rem 0px 4rem;color:#fff}.navbar{margin-top:1.5rem;display:flex;justify-content:center;align-items:center}.navbar li{margin:0px 20px 0px 20px}.navbar li a{text-decoration:none;color:#fff}#rasLogo{position:absolute;width:5rem}.language{display:flex;justify-content:center;align-items:center;color:#fff;font-weight:900}
.unrobot-div{width:100vw;height:100vh;background:#1f1f40;overflow-y:hidden}.grid-wrapper .content{position:relative;grid-row:2;grid-column:1/4;color:#3accd1;display:flex}.grid-wrapper .content .title-content{margin:3rem;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;align-items:center;justify-content:center}.grid-wrapper .content .title-content .UNRobot-img{width:40rem}.grid-wrapper .content .title-content h1{margin:20px;text-transform:uppercase;letter-spacing:2rem;font-weight:800}.grid-wrapper .content .title-content span{-webkit-transform:rotate(90deg);transform:rotate(90deg);-webkit-transform-origin:0px 0px;transform-origin:0px 0px}.owl{position:absolute;right:0;top:0px}.sponsors{background:#fff}.sponsors .sponsors-title{text-transform:uppercase;text-align:center;font-weight:700;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;margin:4rem auto 2rem auto}.sponsors .sponsors-div{margin:auto;width:60%;margin-bottom:4rem}.sponsors .sponsors-div div img{width:150px}section.full{width:100vw;height:100vh}
.category-div{display:grid;height:100%;grid-template-columns:50vw 50vw;grid-template-rows:auto}.category-div .img-div img{width:90%}.category-div .content{grid-column:2;grid-row:1;height:100% !important;width:75%;max-width:500px}.category-div .content .title,.category-div .content .subtitle{text-transform:uppercase;font-weight:600}.category-div .content .title{background:#1f1f40;color:#fff;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;padding:10px 30px 10px 30px;border-radius:10px;font-size:3rem}.category-div .content .subtitle{margin-left:3rem;font-size:6rem}.category-div .content .description{text-align:right;color:#030a1c;padding:3rem 0 3rem 0;width:70%;align-self:flex-end}.category-div .content .reglamento{align-self:flex-end;color:#030a1c;font-weight:600;text-decoration:none}.category-div .content .reglamento::before{content:"->"}
.category-div-dark{background:#1f1f40;display:grid;height:100%;grid-template-columns:50vw 50vw;grid-template-rows:auto;z-index:2}.category-div-dark .img-div-dark{grid-column:2;z-index:inherit}.category-div-dark .img-div-dark img{width:80%}.category-div-dark .content-dark{grid-column:1;grid-row:1;height:100% !important;width:100%;position:relative;z-index:inherit}.category-div-dark .content-dark .decoration-img{position:absolute;left:-20%;margin:auto;top:0;bottom:0;opacity:.04;z-index:1}.category-div-dark .content-dark div{right:2rem;width:75%;position:absolute}.category-div-dark .content-dark .title-dark,.category-div-dark .content-dark .subtitle-dark{font-weight:600;z-index:2}.category-div-dark .content-dark .title-dark{color:#fff;text-align:left;width:105%;font-size:7rem}.category-div-dark .content-dark .subtitle-dark{background:#fff;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;padding:10px 30px 10px 30px;border-radius:10px;color:#1f1f40;margin-left:3rem;font-size:4rem}.category-div-dark .content-dark .description-dark{text-align:left;color:#fff;padding:3rem 0 3rem 0;width:70%;z-index:2;align-self:flex-start}.category-div-dark .content-dark .reglamento-dark{align-self:flex-start;color:#fff;font-weight:600;text-decoration:none}.category-div-dark .content-dark .reglamento-dark::before{content:"->"}
